import React from "react";
import "../styles/pages/mobilePage.css";
import "../styles/components/menu.css";
import "../styles/pages/mainPage.css";
import logo from "../img/logo.png";
import { aboutData } from "../data/aboutData";
import { motion } from "framer-motion";
import AboutColumn from "../components/AboutColumn";

class MobilePage extends React.Component {
  constructor() {
    super();
    this.state = {
      isVisible: false,
      container: {
        hidden: { opacity: 0, scale: 0.8 },
        visible: {
          opacity: 1,
          scale: 1,
        },
      },
      arrowAnimation: {
        hidden: { opacity: 0, scale: 0.9 },
        visible: {
          opacity: 1,
          scale: 1,
        },
      },
      position: false,
    };
    this.toggleMenu = this.toggleMenu.bind(this);
  }

  componentDidMount() {
    window.addEventListener("scroll", this.listenToScroll);
  }

  componentWillUnmount() {
    window.removeEventListener("scroll", this.listenToScroll);
  }

  listenToScroll = () => {
    if (window.pageYOffset / document.documentElement.clientHeight > 1.5) {
      this.setState({
        position: true,
      });
    } else {
      this.setState({
        position: false,
      });
    }
  };

  toggleMenu = () => {
    this.setState({
      isVisible: !this.state.isVisible,
    });
  };

  scrollTop = () => {
    window.scrollTo(0, 0);
  };

  render() {
    const { isVisible, container, arrowAnimation, position } = this.state;
    return (
      <div className="MobilePage">
        {position && (
          <motion.div
            variants={arrowAnimation}
            initial="hidden"
            animate="visible"
            className={position ? "ScrollTop Scrollvisible" : "ScrollTop"}
            onClick={this.scrollTop}
          />
        )}
        <div className="Menu">
          <div className="Menu__mobile">
            <div className="Menu__logo">
              <img src={logo} alt="logo" />
            </div>
            <div className="Menu__toggler">
              <div
                className={
                  isVisible ? "Menu__button button__active" : "Menu__button"
                }
                onClick={this.toggleMenu}
              >
                <div className="Button__burger" />
              </div>
            </div>
          </div>
          <div
            className={`Menu__links ${isVisible ? "Menu__links--visible" : ""}`}
          >
            <a
              className={
                "link" +
                (this.props.active === "mainPage" ? " animation__up" : "") +
                (this.props.before === "mainPage" ? " animation__down" : "")
              }
              href="#main"
            >
              STRONA GWNA
            </a>
            <a
              className={
                "link" +
                (this.props.active === "mainPage" ? " animation__up" : "") +
                (this.props.before === "mainPage" ? " animation__down" : "")
              }
              href="#about"
            >
              O NAS
            </a>
            <a
              className={
                "link" +
                (this.props.active === "mainPage" ? " animation__up" : "") +
                (this.props.before === "mainPage" ? " animation__down" : "")
              }
              href="#map"
            >
              MAPY
            </a>
            <a
              className={
                "link" +
                (this.props.active === "mainPage" ? " animation__up" : "") +
                (this.props.before === "mainPage" ? " animation__down" : "")
              }
              href="#contact"
            >
              KONTAKT
            </a>
          </div>
        </div>
        <div className="Menu__padding" id="main" />
        <div className="mobilePage__Main">
          <motion.div
            className="mobilePage__first"
            variants={container}
            initial="hidden"
            animate="visible"
          >
            <h1>PAULA</h1>
            <h2>CENTRUM MATERIAW BUDOWLANYCH</h2>
          </motion.div>
          <div className="Mobile__title">
            <h1>KIM JESTEMY</h1>
          </div>
          <div className="main__content">
            <p>
              Nasza hurtownia PAULA powstaa ponad 20 lat temu, a dokadniej w
              1998 w Somnikach. Z roku na rok nasza oferta ulega rozbudowie,
              tak aby w maksymalnym stopniu dostosowa j do potrzeb naszych
              klientw. Z czasem zdobywalimy zaufanie coraz wikszej liczby
              klientw (ponad 20 tysicy), a dzi dostarczamy materiay do
              odbiorcw w caej Maopolsce i nie tylko.W 2010 roku powsta nasz
              drugi oddzia w Miechowie. W tej chwili proponujemy gam
              materiaw budowlanych, od materiaw fundamnetowych, poprzez
              ceramik cienn, pokrycia dachowe, a po materiay wykoczeniowe.
              W Somnikach mamy rwnie hurtowni instalacyjn PAULA II WOD-KAN
              C.O. GAZ, ktra rwnie postaa w 1998 roku i oferuje niezbdne
              materiay potrzebne do budowy infrastruktury podziemnej w zakresie
              sieci wodno-kanalizacyjnych, gazowych oraz peen asortyment do
              wewntrznych instalacji.Reasumujc, nasze hurtownie zajmuj si
              kompleksowym zaopatrzeniem materiaowym firm wykonawczych i
              klientw indywidualnych.
            </p>
          </div>
        </div>
        <div className="mobilePage__About" id="about">
          <div className="Mobile__title">
            <h1>CO NAS CECHUJE</h1>
          </div>
          <div className="mainPage__About">
            {aboutData.map((item, id) => {
              return <AboutColumn data={item} key={id} />;
            })}
          </div>
        </div>
        <div className="mobilePage__Map" id="map">
          <div className="Mobile__title">
            <h1>GDZIE NAS ZNALE</h1>
          </div>
          <div className="map__contener">
            <h2>ODDZIA SOMNIKI</h2>
            <iframe
              className="Mobile__map"
              title="oddzialSlomniki"
              src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d2551.7896139065024!2d20.08388305161039!3d50.23983521029808!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x47165294cab2fbf9%3A0x2adcec16e6812c57!2sF.H.U.%20%22PAULA%22%20Pawe%C5%82%20Zieli%C5%84ski-Wojciech%20Podyma%20s%20c!5e0!3m2!1spl!2spl!4v1591528360106!5m2!1spl!2spl"
            />
          </div>
          <div className="map__contener">
            <h2>ODDZIA MIECHW</h2>
            <iframe
              className="Mobile__map"
              title="oddzialMiechow"
              src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d2545.5367980332494!2d20.008533451613044!3d50.35654430190654!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x4717abd2d833cde9%3A0x5a78f744149fd93c!2sF.H.U.%20%22Paula%22%20Pawe%C5%82%20Zieli%C5%84ski%20-%20Wojciech%20Podyma!5e0!3m2!1spl!2spl!4v1591528492798!5m2!1spl!2spl"
            />
          </div>
        </div>
        <div className="mobilePage__RODO" id="RODO">
          <div className="Mobile__title">
            <h1>RODO</h1>
          </div>
          <div className="main__content">
            <p>
              W zwizku z zmian przepisw dotyczcych ochrony danych osobowych,
              okrelan popularnie jako RODO, informujemy, i decydujc si na
              kontakt z nami wyraasz zgod na przetwarzanie Twoich danych
              osobowych przez nasz firm handlowo usugow PAULA Pawe
              Zieliski Wojciech Podyma S.C, z siedzib w Proszowska 7, 32-090
              Somniki, Regon: 366027722, NIP: 682-177-38-87. Gwnym celem
              nowych przepisw jest zapewnienie wikszego bezpieczestwa i
              wikszej ochrony przy przetwarzaniu Twoich danych osobowych.
              Wiadomo ta jest spenieniem obowizku informacyjnego
              wynikajcego z RODO. W trosce o Twoje dane osobowe informujemy, e
              administratorem Twoich danych osobowych jest firma handlowo
              usugowa PAULA Pawe Zieliski Wojciech Podyma S.C, z siedzib w
              proszowska 7, 32-090 Somniki, Regon: 366027722, NIP:
              682-177-38-87 (dalej PAULA). Pamitaj, e przysuguje Ci prawo do
              wgldu do swoich danych, prawo do cofnicia zgody na ich
              przetwarzanie, prawo do dania ich sprostowania, poprawiania,
              usunicia lub ograniczenia przetwarzania, a take do przenoszenia
              danych, wyraenia sprzeciwu i wniesienia skargi do organu
              nadzorczego. Uprawnienie to moesz zrealizowa kontaktujc si z
              nami pisemnie na adres: Firma handlowo usugowa PAULA Pawe
              Zieliski Wojciech Podyma, Proszowska 7, 32-090 Somniki lub
              poprzez adres email: paulaslomniki@o2.pl, paulamiechow@onet.pl
              Twoje dane bd przetwarzane na podstawie udzielonej przez Ciebie
              zgody w celu odpowiedzi na zapytania informacyjne i ofertowe,
              wykonywania czynnoci niezbdnych do realizacji zawartych umw
              (umowy sprzeday, zakupu, najmu, transportu), oraz realizacji
              obowizkw wynikajcych z przepisw prawa. Ponadto Twoje dane
              osobowe mog by przetwarzane w celach: ustalenia, dochodzenia lub
              obrony przed ewentualnymi roszczeniami zwizanymi z realizacj
              zawartych umw. Twoje dane przechowywane bd do momentu wycofania
              przez Ciebie zgody, a po takim wycofaniu, przez okres
              przedawnienia roszcze przysugujcych administratorowi danych i w
              stosunku do niego, lub do momentu wyganiecia obowizkw
              wynikajcych z przepisw prawa. Wycofanie zgody nie ma wpywu na
              zgodno z prawem przetwarzania, ktrego dokonano na podstawie
              zgody przed jej wycofaniem. Twoje dane mog by ujawnione
              podmiotom zajmujcym si obsug informatyczn i ksigow
              administratora danych, ktrzy zapewniaj bezpieczestwo danych
              osobowych i peni rol odbiorcw w rozumieniu prawa.
            </p>
          </div>
        </div>
        <div className="mobilePage__Contact" id="contact">
          <div className="Mobile__title Mobile__contactTitle">
            <h1>KONTAKT</h1>
            <div className="contact__content">
              <div className="content__pause" />
              <h2>ODDZIA SOMNIKI</h2>
              <h3>ul. Proszowska 23</h3>
              <p>pn-pt 7-18</p>
              <p>sobota 7-14</p>
              <a href="tel:123883062">
                <p>tel. 12 388 30 62</p>
              </a>
              <a href="tel:123880340">
                <p>tel. 12 388 03 40</p>
              </a>
              <div className="content__pause" />
              <h2>ODDZIA MIECHW</h2>
              <p>ul. Sienkiewicza 53</p>
              <p>pn-pt 7-17</p>
              <p>sobota 7-14</p>
              <a href="tel:413840400">
                <p>tel. 41 38 40 400</p>
              </a>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default MobilePage;
